.card-edit {
   
    background-color: rgb(255, 254, 254);
    display: flex;
    align-items: center;
    justify-content: center;
    position: sticky;
    right: 0;
    width: 100%;
    height: 20vh;
    background-color: white;
    position: absolute;
}

#dark .card-edit {
   
    background-color: rgb(63, 63, 63);
    display: flex;
    align-items: center;
    justify-content: center;
    position: sticky;
    right: 0;
    width: 100%;
    height: 20vh;
    background-color: rgb(63, 63, 63);
    position: absolute;
}


